<template>
  <div id="Login" style="text-align: center" class="Login">
    <div class="Login-con">
      <Card icon="log-in" title="欢迎登录" :bordered="false">

        <div class="form-con">

          <Form ref="formInline" :model="formInline" :rules="ruleInline">
            <!--输入账号项-->
            <FormItem prop="user">
              <Input type="text" v-model="formInline.user" placeholder="账号" autofocus>
                <Icon type="ios-person-outline" slot="prepend"></Icon>
              </Input>
            </FormItem>
            <!--密码项-->
            <FormItem prop="password">
              <Input type="password" v-model="formInline.password" placeholder="密码">
                <Icon type="ios-lock-outline" slot="prepend"></Icon>
              </Input>
            </FormItem>
            <!--登录按钮项-->
            <FormItem>
              <Button type="primary" @click="handleSubmit('formInline')" long="334px" >登录</Button>
            </FormItem>
            <!--最下面的一句话-->
            <a class="Login-tip">没有账号？前往注册</a>
          </Form>

        </div>

      </Card>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        formInline: {
          user: '',
          password: ''
        },
        ruleInline: {
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { type: 'string', min: 6,max: 18, message: '密码长度必须在6-18位之间', trigger: 'blur' }
          ],
          user:[
            { required: true, message: '请输入账号', trigger: 'blur' },
            { type: 'string', min: 2,max: 12, message: '账号长度必须在2-12位之间', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      handleSubmit(user) {
        this.$refs[user].validate((valid) => {
          if (valid) {
            this.$Message.success('符合要求!');
          } else {
            this.$Message.error('不符合要求!');
          }
        })
      }
    }
  }
</script>

<style lang="less">
  @import 'login.less';
</style>



